import React, { useEffect, useState, useCallback } from "react";
import { useHistory } from "react-router";
import Password from "../../Components/Password/Password";
import "./Passwords.css";
import "react-responsive-modal/styles.css";
import { Modal } from "react-responsive-modal";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { saveNewPassword, deleteAPassword, checkAuthenticated } from "../../axios/instance";
import { useSelector, useDispatch } from "react-redux";
import { setAuth, setPasswords } from "../../redux/actions";

function Passwords() {
  const [platform, setPlatform] = useState("");
  const [platEmail, setPlatEmail] = useState("");
  const [platPass, setPlatPass] = useState("");
  const [open, setOpen] = useState(false);

  const history = useHistory();
  const dispatch = useDispatch();
  const { isAuthenticated, name, email, passwords } = useSelector((state) => state);

  // âœ… Stable function to fetch user and passwords
  const verifyUser = useCallback(async () => {
    try {
      const res = await checkAuthenticated();
      if (res.status === 200) {
        dispatch(setPasswords(res.data.passwords || []));
      } else {
        dispatch(setAuth(false));
      }
    } catch (err) {
      console.log(err);
      dispatch(setAuth(false));
    }
  }, [dispatch]);

  // Fetch passwords on mount
  useEffect(() => {
    if (!isAuthenticated) {
      history.replace("/signin");
    } else {
      verifyUser();
    }
  }, [isAuthenticated, history, verifyUser]);

  // Add new password
  const addNewPassword = async (e) => {
    e.preventDefault();
    try {
      const data = { platform, userPass: platPass, platEmail, userEmail: email };
      const res = await saveNewPassword(data);

      if (res.status === 200) {
        toast.success(res.data.message, { position: "top-right", autoClose: 5000 });
        dispatch(setPasswords(res.data.passwords || []));
        setOpen(false);
        setPlatform("");
        setPlatEmail("");
        setPlatPass("");
      } else {
        toast.error(res.data.error || "Could not add password", { position: "top-right", autoClose: 5000 });
      }
    } catch (err) {
      console.log(err);
      toast.error("Failed to add password", { position: "top-right", autoClose: 5000 });
    }
  };

  // Delete password
  const handleDelete = async (id) => {
    try {
      const res = await deleteAPassword({ id });
      if (res.status === 200) {
        toast.success(res.data.message, { position: "top-right", autoClose: 5000 });
        dispatch(setPasswords(res.data.passwords || []));
      } else {
        toast.error(res.data.error || "Could not delete password", { position: "top-right", autoClose: 5000 });
      }
    } catch (err) {
      console.log(err);
      toast.error("Failed to delete password", { position: "top-right", autoClose: 5000 });
    }
  };

  return (
    <div className="passwords">
      <ToastContainer />
      <h1>
        Welcome <span className="name">{name}</span>
      </h1>

      <div className="modal">
        <button className="modalButton" type="button" onClick={() => setOpen(true)}>
          Add New Password
        </button>

        <Modal open={open} onClose={() => setOpen(false)}>
          <h2>Add a new password</h2>
          <form className="form">
            <div className="form__inputs">
              <label htmlFor="platform">Platform</label>
              <input
                id="platform"
                type="text"
                placeholder="E.g. Facebook"
                value={platform}
                onChange={(e) => setPlatform(e.target.value)}
                required
              />
            </div>

            <div className="form__inputs">
              <label htmlFor="platEmail">Email</label>
              <input
                id="platEmail"
                type="email"
                placeholder="E.g. user@gmail.com"
                value={platEmail}
                onChange={(e) => setPlatEmail(e.target.value)}
                required
              />
            </div>

            <div className="form__inputs">
              <label htmlFor="platPass">Password</label>
              <input
                id="platPass"
                type="password"
                placeholder="Password"
                value={platPass}
                onChange={(e) => setPlatPass(e.target.value)}
                required
              />
            </div>

            <button type="button" onClick={addNewPassword}>
              Add
            </button>
          </form>
        </Modal>
      </div>

      <hr />

      <div className="passwords__list">
        {passwords?.length > 0 ? (
          passwords.map((data) => (
            <Password
              key={data._id}
              id={data._id}
              name={data.platform}
              password={data.password}
              email={data.platEmail}
              iv={data.iv}
              onDelete={handleDelete} // Pass delete handler
            />
          ))
        ) : (
          <div className="nopass">
            <p>You have not added any passwords yet.</p>
            <button className="modalButton" type="button" onClick={() => setOpen(true)}>
              Try Adding a password now
            </button>
          </div>
        )}
      </div>
    </div>
  );
}

export default Passwords;
